import {observable,autorun,toJS,action} from "mobx";



class Stock{
    @observable data = ["dragonQuest","finalFantasy"];
    @observable counter = 0;
    @observable rest = [];


    increase() {
        this.counter = this.counter + 1;
    }
    decrease() {
        this.counter = this.counter - 1;
    }

    @action
    setdata = (val) =>{
        this.rest.toJS(val)
        console.log(this.rest)
    }

}


let stock = new Stock;

export default stock;

